.timeline-container {
  width: 100%;
  min-width: 800px;
  margin: 0 auto;
  padding: 40px 20px;
  background: linear-gradient(135deg, #f5f7fa 0%, #c3cfe2 100%);
  min-height: 100vh;
}

.timeline-header {
  text-align: center;
  margin-bottom: 60px;
  color: #2c3e50;
}

.timeline-header h2 {
  font-size: 2.5rem;
  font-weight: 700;
  margin: 0 0 10px 0;
  text-shadow: 0 2px 4px rgba(255, 255, 255, 0.5);
}

.timeline-header p {
  font-size: 1.2rem;
  opacity: 0.9;
  margin: 0;
}

.timeline {
  position: relative;
  width: 100%;
  max-width: 1000px;
  margin: 0 auto;
}

.timeline::before {
  content: '';
  position: absolute;
  left: 50px;
  top: 0;
  bottom: 0;
  width: 4px;
  background: linear-gradient(
    to bottom,
    #ff6b6b,
    #4ecdc4,
    #45b7d1,
    #96ceb4,
    #feca57
  );
  border-radius: 2px;
  box-shadow: 0 0 10px rgba(255, 255, 255, 0.3);
}

.timeline-item {
  position: relative;
  margin-bottom: 60px;
  display: flex;
  align-items: flex-start;
}

.timeline-marker {
  position: relative;
  flex-shrink: 0;
}

.marker-icon {
  width: 100px;
  height: 100px;
  background: linear-gradient(135deg, #ff6b6b 0%, #ff8e53 100%);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0 8px 25px rgba(255, 107, 107, 0.4);
  border: 4px solid white;
  position: relative;
  z-index: 2;
}

.marker-icon .city-icon {
  font-size: 2rem;
  color: white;
  text-shadow: 0 2px 4px rgba(0, 0, 0, 0.3);
}

.marker-line {
  position: absolute;
  top: 100px;
  left: 50%;
  transform: translateX(-50%);
  width: 2px;
  height: 40px;
  background: linear-gradient(to bottom, rgba(255, 255, 255, 0.8), transparent);
}

.timeline-content {
  flex: 1;
  margin-left: 40px;
  margin-top: 10px;
}

.city-card {
  background: rgba(255, 255, 255, 0.95);
  border-radius: 20px;
  padding: 30px;
  box-shadow: 0 15px 35px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
}

.city-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 4px;
  background: linear-gradient(
    90deg,
    #ff6b6b,
    #4ecdc4,
    #45b7d1,
    #96ceb4,
    #feca57
  );
}

.city-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.15);
}

.city-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 15px;
}

.city-name {
  font-size: 1.8rem;
  font-weight: 700;
  color: #2c3e50;
  margin: 0;
  line-height: 1.2;
}

.city-name-en {
  display: block;
  font-size: 1.2rem;
  font-weight: 400;
  color: #7f8c8d;
  margin-top: 5px;
}

.city-dates {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  padding: 8px 16px;
  border-radius: 20px;
  font-weight: 600;
  font-size: 0.9rem;
  white-space: nowrap;
}

.city-country {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
  padding: 10px 15px;
  background: rgba(102, 126, 234, 0.1);
  border-radius: 15px;
  border: 1px solid rgba(102, 126, 234, 0.2);
}

.country-flag {
  font-size: 1.2rem;
  margin-right: 10px;
}

.country-name {
  font-weight: 600;
  color: #667eea;
  font-size: 1rem;
}

.transport-info {
  display: flex;
  align-items: center;
  padding: 12px 15px;
  margin: 8px 0;
  border-radius: 12px;
  font-size: 0.9rem;
}

.transport-info.arrival {
  background: rgba(76, 175, 80, 0.1);
  border: 1px solid rgba(76, 175, 80, 0.3);
  color: #2e7d32;
}

.transport-info.departure {
  background: rgba(255, 152, 0, 0.1);
  border: 1px solid rgba(255, 152, 0, 0.3);
  color: #e65100;
}

.transport-icon {
  font-size: 1.1rem;
  margin-right: 10px;
}

.transport-detail {
  flex: 1;
  font-weight: 500;
}

.transport-time {
  font-weight: 600;
  font-size: 0.8rem;
  opacity: 0.8;
}

/* 响应式设计 */
@media (max-width: 800px) {
  .timeline-container {
    min-width: 100%;
    padding: 20px 10px;
  }

  .timeline-header h2 {
    font-size: 2rem;
  }

  .timeline::before {
    left: 30px;
  }

  .marker-icon {
    width: 60px;
    height: 60px;
  }

  .marker-icon .city-icon {
    font-size: 1.5rem;
  }

  .timeline-content {
    margin-left: 20px;
  }

  .city-card {
    padding: 20px;
  }

  .city-name {
    font-size: 1.5rem;
  }

  .city-header {
    flex-direction: column;
    align-items: flex-start;
  }

  .city-dates {
    margin-top: 10px;
  }
}

/* 动画效果 */
@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.timeline-item {
  animation: fadeInUp 0.6s ease-out forwards;
}

.timeline-item:nth-child(1) {
  animation-delay: 0.1s;
}
.timeline-item:nth-child(2) {
  animation-delay: 0.2s;
}
.timeline-item:nth-child(3) {
  animation-delay: 0.3s;
}
.timeline-item:nth-child(4) {
  animation-delay: 0.4s;
}
.timeline-item:nth-child(5) {
  animation-delay: 0.5s;
}
.timeline-item:nth-child(6) {
  animation-delay: 0.6s;
}
.timeline-item:nth-child(7) {
  animation-delay: 0.7s;
}
.timeline-item:nth-child(8) {
  animation-delay: 0.8s;
}
.timeline-item:nth-child(9) {
  animation-delay: 0.9s;
}
.timeline-item:nth-child(10) {
  animation-delay: 1s;
}
.timeline-item:nth-child(11) {
  animation-delay: 1.1s;
}
.timeline-item:nth-child(12) {
  animation-delay: 1.2s;
}
.timeline-item:nth-child(13) {
  animation-delay: 1.3s;
}
.timeline-item:nth-child(14) {
  animation-delay: 1.4s;
}
.timeline-item:nth-child(15) {
  animation-delay: 1.5s;
}
